.HeaderDivMain{
    width: 100%;
    /* background: #370063;   */
    background: #fe7e00;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 2;
    box-shadow: 1px 1px 15px rgb(105, 105, 107);

}

.Headername{
    color: #fff;
    font-size: 24px; 
    z-index: 5;
    padding-left: 10px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

#Tagline{
    font-size:16px;
    bottom:0px;
    padding-bottom: 5px;
    margin-left:10px;
    color: #fff;
    
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.Taglinediv{
    z-index: 5;
    display: flex;
    flex-direction: column;
    
}